import * as React from "react";

import "./PosterContainer.css";



const Poster = (props: IPoster_P) => (
  <div className="poster">
    <a href={props.poster.url}>
      <figure>
        <img src={`${process.env.PUBLIC_URL}/images/${props.poster.filename}`} />
      </figure>
    </a>
    <div className="poster_title">
      {props.poster.title}
    </div>
  </div>
);

const PosterContainer = (props: IPosterContainer_P) => (
  <div id="poster_container">
    {props.data.map( (x, i) => <Poster poster={x} key={`data${i}`} />)}
  </div>
);



export default PosterContainer;


