const testData: IPoster_D[] = [
  {
    category: "Game Jam",
    filename: "firesaurus_rex.jpg",
    title: "Firesaurus Rex",
    url: "http://jfaw-love.itch.io/firesaurus_rex",
  },
  {
    category: "Game Jam",
    filename: "dungeon_profiteer.jpg",
    title: "Dungeon Profiteer",
    url: "https://taqueso.itch.io/dungeon-profiteer",
  },
  {
    category: "Released Product",
    filename: "galacide.jpg",
    title: "Galacide",
    url: "http://galacide.com/",
  },
  {
    category: "Game Jam",
    filename: "bodega_blast.jpg",
    title: "Bodega Blast",
    url: "http://www.awfuljams.com/little-awful-2017/games/bodega-blast",
  },
  {
    category: "Tech Demo",
    filename: "prism9.jpg",
    title:"Prism 9",
    url: "https://www.patreon.com/jfaw",
  },
  {
    category: "Game Mod",
    filename: "arma3_racing.jpg",
    title: "Arma 3 Racing",
    url: "https://www.youtube.com/watch?v=VrjYeEE5RhY",
  },
  {
    category: "Released Product",
    filename: "delteria.jpg",
    title: "Detleria",
    url: "http://delteria.graalonline.com/",
  }
];



ReactDOM.render(
  <PosterContainer data={testData} />,
  document.getElementById("root") as HTMLElement
);
registerServiceWorker();



